import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { Headers, Http, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { Storage } from '@ionic/storage';

//import { URL } from '@angular/url-parse';



//@IonicPage()
@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})

export class ContactPage {

  token: String;
  greeting;

  constructor(public navCtrl: NavController, public http: Http, private storage: Storage) { 
    
  }

  ionViewDidLoad() {
    
this.storage.get('groovyKey').then((val) => {
  let token = val;
  
console.log('retrieved token: ' + token);

//skenney - 1003682710
//fun - 1003890650


//configuring for remote hosting on firebase
//this.http.get('http://localhost:8100/api/v1/profile/me', options)
//https://profile-testing-7452b.firebaseapp.com/api/v1/profile/me

let pomaHeaders = new Headers;
pomaHeaders.set('GET', '/api/v1/profile/me HTTP/1.1');
pomaHeaders.append('Accept', 'application/json'); 
pomaHeaders.append('Authorization', 'Bearer ' + token);
pomaHeaders.append('Cache-Control', 'no-cache');


let options = new RequestOptions({
  headers: pomaHeaders
});

/* res.text OR res.json */


this.http.get('http://localhost:8100/api/v1/profile/me', options)
.map(res => res.json())
.subscribe(data => {

var myResponse = JSON.stringify(data);
// WITHOUT THE STRINGIFY ABOVE, WE JUST GET [OBJECT OBJECT]


console.log('myRes ' + myResponse);

let firstName = myResponse.split('First]":"')[1];
firstName = firstName.substring(0, firstName.indexOf('","'));
//alert('|'+firstName+'|');
let lastName = myResponse.split('Last]":"')[1];
lastName = lastName.substring(0, lastName.indexOf('","'));
//alert('|'+lastName+'|');
let suffix = myResponse.split('Suffix]":"')[1];
suffix = suffix.substring(0, suffix.indexOf('","'));
//alert('|'+suffix+'|');

let member_type = myResponse.split('Member Type]":"')[1];
member_type = member_type.substring(0, member_type.indexOf('","'));

alert('member type |' + member_type + '|');

if (suffix == 'D.O.')
{
  this.greeting = "Greetings Dr. " + lastName + ',';
} else
{
  this.greeting = "Greetings " + firstName + lastName + ',';
}

console.log('last |' + lastName + '|');

}, error => {
    console.log('error: ' + error);
});


});

  }
}


